</template>

<script>
import { db } from '../main'

export default {
  data () {
    return {
      md:'',
      signinMember:'',
      logined:false,
      page: '0',
      party: '',
      gift:'',
      admin:{
        name:'',
        time:'',
        device:'',
      },
      user:{
        data:{},
        time:'',
        datetime:'',
        admin:{},
      },
      userGift:{
        data:{},
        giftTitle:'',
        time:'',
        datetime:'',
        admin:{},
      },
      cameraSettings: {
        audio: false,
        video: {
          facingMode: { ideal: 'environment' },
        }
      },
      paused: false,
      content:'',
      isLogin:'',
      loginOK: false,
      grtGiftOK: false,
      giftTitle:'',
      errorQRcode:'',
    }
  },
  firestore () {
    return {
      party: db.collection('party').orderBy('time','desc'),
      gift: db.collection('gift').orderBy('time','desc'),
    }
  },
  computed: {
    icsAllMembaer(){
      let members = 0
      let filterDepartment = this.party.filter(function(item, index, array){
        if (item.data.gid.indexOf('ICS')>=0){
          members +=1;
        }
      });
      return members;
    },
    hgAllMembaer(){
      let members = 0
      let filterDepartment = this.party.filter(function(item, index, array){
        if (item.data.gid.indexOf('HG')>=0){
          members +=1;
        }
      });
      return members;
    },
    hbAllMembaer(){
      let members = 0
      let filterDepartment = this.party.filter(function(item, index, array){
        if (item.data.gid.indexOf('HB')>=0){
          members +=1;
        }
      });
      return members;
    },
    lAllMembaer(){
      let members = 0
      let filterDepartment = this.party.filter(function(item, index, array){
        if (item.data.gid.indexOf('L')>=0){
          members +=1;
        }
      });
      return members;
    }
  },
  methods: {
    signin() {
      if(this.signinMember){
        window.localStorage.setItem('admin', this.signinMember);
        window.localStorage.setItem('device', this.md.ua);
        window.localStorage.setItem('loginTime', this.getNowTime());
        this.admin.name = this.signinMember;
        this.admin.time =  this.md.ua;
        this.admin.device = this.getNowTime();
        this.logined= true;
        this.page = '1';
      }
    },
    checkLogined(){
      if(window.localStorage.admin){
        this.admin.name = window.localStorage.admin;
        this.admin.time = window.localStorage.loginTime;
        this.admin.device = window.localStorage.device;
        this.logined= true;
        this.page = '1';
      } else {
        this.logined= false;
        this.page = '0';
      }
      window.localStorage.setItem('loginTime', this.getNowTime());
    },
    handleSelect(key, keyPath) {
      this.page = key;
      this.content = '';
      this.errorQRcode = ''
      this.giftTitle = '';
      this.isLogin = '';
      this.unPauseCamera ();
    },
    addUser(){
      this.user.data = this.content;
      this.user.time = Date.now();
      this.user.datetime =  this.getNowTime();
      this.user.admin =  this.admin;
      db.collection('party').add(this.user).then(() => {
          this.user.data={};
          this.content={};
          this.giftTitle = '';
          this.unPauseCamera();
        });
    },
    addGift(){
      this.userGift.data = this.content;
      this.userGift.giftTitle = this.giftTitle;
      this.userGift.time =  Date.now();
      this.userGift.datetime =  this.getNowTime();
      this.userGift.admin =  this.admin;
      db.collection('gift').add(this.userGift).then(() => {
          this.userGift.data = ''
          this.content={};
          this.giftTitle = '';
          this.unPauseCamera();
        });
    },
    deleteData (id) {
      db.collection('party').doc(id).delete()
    },
    deleteGift(id) {
      db.collection('gift').doc(id).delete()
    },
    async onDecode (content) {
      try {
        this.content = JSON.parse(content);
        this.pauseCamera();
        if(this.page==='1'){
          let result = this.party.map((item, index)=>item.data.gid).indexOf(this.content.gid);
          console.log(result)
          if(result>=0){
            this.isLogin = '已登入'
            this.loginOK = false; 
          } else{
            this.loginOK = true; 
          }
        }else if(this.page==='2'){
          let result = this.gift.map((item, index)=>item.data.gid).indexOf(this.content.gid);
          if(result>=0){
            this.isLogin = '已登入'
            this.grtGiftOK = false; 
          } else{
            this.grtGiftOK = true; 
          }
        }
      } catch (error) {
        this.errorQRcode = '未知的QRcode';
        this.pauseCamera();
      }
    },
    pauseCamera () {
      this.paused = true;
    },
    unPauseCamera () {
      this.content = '';
      this.errorQRcode = '';
      this.isLogin = '';
      this.paused = false;
      this.loginOK = false; 
      this.grtGiftOK = false; 
    },
    sendLoginData(){
      // const url = 'http://xxx.com/?uid=' + this.content;
      // this.LoginData.push(url);
    },
    sendAwardData(){
      // const url = 'http://xxx.com/?uid=' +this.content+'&giftTitle='+this.giftTitle;
      // this.AwardData.push(url);
    },
    getNowTime(){
      const NowDate = new Date();
      let month = (NowDate.getMonth()+1);
      if(month<10) month = "0"+month;
      let day = NowDate.getDate();
      if(day<10) day = "0"+day;
      let h=NowDate.getHours();
      if(h<10) h = "0"+h;
      let m=NowDate.getMinutes();
      if(m<10) m = "0"+m;
      let s=NowDate.getSeconds();
      if(s<10) s = "0"+s;
      return  NowDate.getFullYear()+ "-" + month+ "-"+ day+" "+ h+':'+m+':'+s ;
    }
  },
  mounted () {
    this.md = new MobileDetect(window.navigator.userAgent);
    if(window.localStorage.loginTime){
      let s1 = new Date(window.localStorage.loginTime);
      let s2 = new Date(this.getNowTime());
      let s3 = s2.getTime() - s1.getTime();
      if(s3 > 86400000){
        window.localStorage.removeItem('member');
        window.localStorage.removeItem('device');
        window.localStorage.removeItem('loginTime');
      }
    }
    this.checkLogined()
  }
}
</script>

